<template>
  <div class="page-container">
    <div class="notes">
      <div class="page-header">
        <div class="header-content">
          <h1>随手记</h1>
          <p>记录喵星人的点点滴滴</p>
        </div>
        <div class="add-buttons">
          <button class="add-btn" @click="showAddDialog">
            <i class="el-icon-plus"></i>
            添加记录
          </button>
        </div>
      </div>

      <div class="pets-filter">
        <div class="pets-list">
          <div v-for="pet in pets" :key="pet.id" class="pet-item"
            :class="{ active: selectedPetId === pet.id }" @click="selectPet(pet.id)">
            <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
            <span class="pet-name">{{ pet.name }}</span>
          </div>
        </div>
      </div>

      <div class="type-filter">
        <div v-for="type in noteTypes" :key="type.value" class="type-tag"
          :class="{ active: selectedTypes.includes(type.value) }" @click="toggleType(type.value)">
          <span class="type-icon">{{ type.icon }}</span>
          <span class="type-label">{{ type.label }}</span>
        </div>
      </div>

      <div class="record-list">
        <div v-for="note in filteredNotes" :key="note.id" class="record-card">
          <div class="record-date">{{ note.date }}</div>
          <div class="record-content">
            <div class="record-header">
              <div class="pet-info">
                <img :src="note.petAvatar" :alt="note.petName" class="pet-avatar">
                <span class="pet-name">{{ note.petName }}</span>
              </div>
              <div class="record-type" :class="note.type">
                <span class="type-icon">{{ getTypeIcon(note.type) }}</span>
                <span class="type-label">{{ getTypeLabel(note.type) }}</span>
              </div>
            </div>
            <div class="record-body">
              <h3 class="record-title">{{ note.title }}</h3>
              <p class="record-description">{{ note.content }}</p>
              <div v-if="note.images && note.images.length > 0" class="image-gallery">
                <div v-for="(image, index) in note.images" :key="index" class="image-item">
                  <img :src="image" :alt="note.title" @click="previewImage(image)">
                </div>
              </div>
            </div>
            <div class="record-actions">
              <button class="action-btn edit" @click="editNote(note)">
                <span class="icon">✏️</span>
                <span>编辑</span>
              </button>
              <button class="action-btn delete" @click="deleteNote(note)">
                <span class="icon">🗑️</span>
                <span>删除</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <!-- 添加/编辑记录对话框 -->
      <div v-if="dialogVisible" class="dialog-overlay">
        <div class="dialog-content">
          <div class="dialog-header">
            <h2>{{ dialogType === 'add' ? '添加记录' : '编辑记录' }}</h2>
            <button class="close-btn" @click="dialogVisible = false">×</button>
          </div>
          <div class="dialog-body">
            <div class="form-group">
              <label>选择宠物</label>
              <div class="pets-select">
                <div v-for="pet in pets" :key="pet.id" class="pet-option"
                  :class="{ active: noteForm.petId === pet.id }" @click="noteForm.petId = pet.id">
                  <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                  <span class="pet-name">{{ pet.name }}</span>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label>记录类型</label>
              <div class="type-select">
                <div v-for="type in noteTypes" :key="type.value" class="type-option"
                  :class="{ active: noteForm.type === type.value }"
                  @click="noteForm.type = type.value">
                  <span class="type-icon">{{ type.icon }}</span>
                  <span class="type-label">{{ type.label }}</span>
                </div>
              </div>
            </div>
            <div class="form-group">
              <label>日期</label>
              <input type="date" v-model="noteForm.date" class="form-input">
            </div>
            <div class="form-group">
              <label>标题</label>
              <input type="text" v-model="noteForm.title" class="form-input" placeholder="请输入标题">
            </div>
            <div class="form-group">
              <label>内容</label>
              <textarea v-model="noteForm.content" class="form-textarea"
                placeholder="请输入详细内容"></textarea>
            </div>
            <div class="form-group">
              <label>图片</label>
              <div class="image-upload">
                <div v-for="(image, index) in noteForm.images" :key="index" class="image-preview">
                  <img :src="image" :alt="noteForm.title">
                  <button class="remove-image" @click="removeImage(index)">×</button>
                </div>
                <div class="upload-btn" @click="triggerFileInput">
                  <i class="el-icon-plus"></i>
                  <span>添加图片</span>
                </div>
                <input type="file" ref="fileInput" @change="handleImageUpload" multiple accept="image/*"
                  style="display: none">
              </div>
            </div>
          </div>
          <div class="dialog-footer">
            <button class="cancel-btn" @click="dialogVisible = false">取消</button>
            <button class="confirm-btn" @click="submitForm">确定</button>
          </div>
        </div>
      </div>

      <!-- 图片预览 -->
      <div v-if="previewImageUrl" class="image-preview-overlay" @click="previewImageUrl = null">
        <img :src="previewImageUrl" :alt="'预览图片'">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Notes',
  data() {
    return {
      pets: [
        {
          id: 1,
          name: '小灰灰',
          avatar: require('@/assets/normal_cat.png')
        },
        {
          id: 2,
          name: '花花',
          avatar: require('@/assets/cute_cat.png')
        }
      ],
      noteTypes: [
        { value: 'daily', label: '日常', icon: '📝' },
        { value: 'behavior', label: '行为', icon: '🎭' },
        { value: 'growth', label: '成长', icon: '🌱' }
      ],
      notes: [
        {
          id: 1,
          date: '2024-03-20',
          petId: 1,
          petName: '小灰灰',
          petAvatar: require('@/assets/normal_cat.png'),
          type: 'daily',
          title: '今天特别开心',
          content: '今天小灰灰特别开心，一直在玩逗猫棒，还学会了新技能！',
          images: [
            require('@/assets/normal_cat.png'),
            require('@/assets/cute_cat.png')
          ]
        }
      ],
      dialogVisible: false,
      dialogType: 'add',
      noteForm: {
        petId: '',
        type: '',
        date: '',
        title: '',
        content: '',
        images: []
      },
      selectedPetId: null,
      selectedTypes: [],
      previewImageUrl: null
    }
  },
  computed: {
    filteredNotes() {
      let notes = [...this.notes]

      if (this.selectedPetId) {
        notes = notes.filter(note => note.petId === this.selectedPetId)
      }

      if (this.selectedTypes.length > 0) {
        notes = notes.filter(note => this.selectedTypes.includes(note.type))
      }

      return notes.sort((a, b) => new Date(b.date) - new Date(a.date))
    }
  },
  methods: {
    getTypeIcon(type) {
      const typeInfo = this.noteTypes.find(t => t.value === type)
      return typeInfo ? typeInfo.icon : '📝'
    },
    getTypeLabel(type) {
      const typeInfo = this.noteTypes.find(t => t.value === type)
      return typeInfo ? typeInfo.label : '其他'
    },
    selectPet(petId) {
      this.selectedPetId = this.selectedPetId === petId ? null : petId
    },
    toggleType(type) {
      const index = this.selectedTypes.indexOf(type)
      if (index === -1) {
        this.selectedTypes.push(type)
      } else {
        this.selectedTypes.splice(index, 1)
      }
    },
    showAddDialog() {
      this.dialogType = 'add'
      this.noteForm = {
        petId: this.selectedPetId,
        type: '',
        date: new Date().toISOString().split('T')[0],
        title: '',
        content: '',
        images: []
      }
      this.dialogVisible = true
    },
    editNote(note) {
      this.dialogType = 'edit'
      this.noteForm = { ...note }
      this.dialogVisible = true
    },
    deleteNote(note) {
      if (confirm('确认删除该记录吗？')) {
        this.notes = this.notes.filter(n => n.id !== note.id)
      }
    },
    submitForm() {
      if (this.dialogType === 'add') {
        const pet = this.pets.find(p => p.id === this.noteForm.petId)
        this.notes.push({
          id: this.notes.length + 1,
          petName: pet.name,
          petAvatar: pet.avatar,
          ...this.noteForm
        })
      } else {
        const index = this.notes.findIndex(n => n.id === this.noteForm.id)
        const pet = this.pets.find(p => p.id === this.noteForm.petId)
        this.notes[index] = {
          ...this.noteForm,
          petName: pet.name,
          petAvatar: pet.avatar
        }
      }
      this.dialogVisible = false
    },
    triggerFileInput() {
      this.$refs.fileInput.click()
    },
    handleImageUpload(event) {
      const files = event.target.files
      for (let i = 0; i < files.length; i++) {
        const file = files[i]
        const reader = new FileReader()
        reader.onload = (e) => {
          this.noteForm.images.push(e.target.result)
        }
        reader.readAsDataURL(file)
      }
      event.target.value = ''
    },
    removeImage(index) {
      this.noteForm.images.splice(index, 1)
    },
    previewImage(image) {
      this.previewImageUrl = image
    }
  }
}
</script>

<style scoped>
.image-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
  margin-top: 15px;
}

.image-item {
  position: relative;
  padding-bottom: 100%;
  cursor: pointer;
  border-radius: 8px;
  overflow: hidden;
}

.image-item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.image-item:hover img {
  transform: scale(1.05);
}

.image-upload {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 10px;
}

.image-preview {
  position: relative;
  padding-bottom: 100%;
  border-radius: 8px;
  overflow: hidden;
}

.image-preview img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.remove-image {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

.upload-btn {
  padding-bottom: 100%;
  position: relative;
  border: 2px dashed #ddd;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f9f9f9;
  transition: all 0.3s ease;
}

.upload-btn:hover {
  border-color: #409EFF;
  background: #f0f9ff;
}

.upload-btn i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
  color: #909399;
}

.image-preview-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  cursor: pointer;
}

.image-preview-overlay img {
  max-width: 90%;
  max-height: 90vh;
  object-fit: contain;
}
</style> 